import React from 'react';
import Shake from 'shake.js';

class ShakeDetect extends React.Component {
  shake;

  constructor(props) {
    super(props);
    this.state = {date: new Date(), x: 0, y: 0, z: 0, timesShaken: 0};
  }

  componentDidMount() {
    // window.addEventListener("devicemotion", event => {
    //   if(event.acceleration.x > 30) {
    //     this.setState({timesShaken: this.state.timesShaken + 1});
    //   }
    // });
    this.shake = new Shake({
      threshold: 5, // optional shake strength threshold
      timeout: 1000 // optional, determines the frequency of event generation
    });
    this.shake.start();
    window.addEventListener('shake', () => {
      this.setState({timesShaken: this.state.timesShaken + 1});
    }, false);
  }

  componentWillUnmount() {
    this.shake.stop();
  }

  render() {
    return (
      <div>
        <h1>Shake detector!</h1>
        <h1>Shaken {this.state.timesShaken} times.</h1>
      </div>
    );
  }
}

export default ShakeDetect